// This section is for looking up items in the analysis log

export const findEventsByLabel = (log,label) => {
    return log.filter(entry => entry.label === label)
}

export const findEventByNumericalID = (log,numericalID) => {
    return log.find(entry => entry.numericalID === numericalID)
}

export const findEventsBylID = (log,id) => {
    return log.filter(entry => entry.id === id)
}

